/** @format */

import React, { useState, useLayoutEffect } from 'react';
import styled from 'styled-components';
import { Button } from './common/Button';

import { gameStore, GAME_STATES } from '../../stores/gameStore';

const StyledDiv = styled.div`
	position: absolute;
	bottom: 0;
	left: 44%;
	padding: 0.5em;
	height: 24%;
	width: 12%;
	transform: scale3d(1, 1, 2) rotate3d(1, 0, 0, 60deg);
}`;

export function Hud() {
	const [balls, setBalls] = useState(3);
	const [scoreValue, setGamescoreValue] = useState(0);
	useLayoutEffect(() => {
		const subs = gameStore.balls.subscribe(setBalls);
		subs.add(gameStore.scoreValue.subscribe(setGamescoreValue));
		return () => subs.unsubscribe();
	}, []);
	return (
		<StyledDiv>
			<p>Balls: {balls}</p>
			<p>Score: {scoreValue}</p>
			<div>
				<Button
					callback={() => {
						gameStore.resetGame();
						gameStore.setGameState(GAME_STATES.LEVEL_CHOICE);
					}}
					text="Back"
					enabled
				></Button>
			</div>
		</StyledDiv>
	);
}
